import Vue from "vue";
import zircle from "zircle";

import store from "./store";
import App from "./App.vue";
import "./background";

import "zircle/dist/zircle.css";
import "./css/background.css";

Vue.use(zircle);

Vue.config.productionTip = false;

new Vue({
  store,
  render: h => h(App)
}).$mount("#app");
