<template>
  <div id="app">
    <z-canvas :views="$options.components" class="transparente"></z-canvas>
  </div>
</template>

<script>
import { TweenLite } from "gsap";
import { mapMutations } from "vuex";

import home from "./components/home";
import estadistica from "./components/vistas/estadistica/index";

const components = {
  home, // : () => import("./components/home")
  estadistica
};

export default {
  components,
  methods: mapMutations(["SET_ANGULO"]),
  mounted() {
    const animacionEstado = {
      angulo: 0
    };

    this.$root.$animacion = TweenLite.to(animacionEstado, 700, {
      angulo: 365,
      repeat: -1,
      onUpdate: () => {
        this.SET_ANGULO({ angulo: animacionEstado.angulo });
      }
    });

    this.$zircle.setView("home");
  }
};
</script>

<style scoped>
.transparente {
  background-color: transparent;
}
</style>
