<template>
  <z-view size="xl" class="centro">
    <p class="titulo">
      Ministerio de las Mujeres, Géneros y Diversidad
    </p>
    <img
      slot="image"
      src="../assets/imagen_portada_mingeneros.jpg"
      width="100%"
      height="100%"
    />

    <section slot="extension">
      <z-spot
        v-for="(item, index) in items"
        :key="index"
        :angle="anguloRelativo(index, items.length, 10)"
        :distance="160"
        :label="item.texto"
        class="primer-nivel"
        @click.native="pausar"
        :to-view="item.view"
      >
        <i class="material-icons" style="font-size: 40px;">{{ item.icon }}</i>

        <!-- <section slot="extension">
          <z-spot
            v-for="(subitem, subindex) in item.items"
            :angle="anguloRelativo(subindex, item.items.length, 50)"
            :distance="120"
            size="xxs"
            :key="subindex"
            style="background-color: #86007D"
          ></z-spot>
        </section> -->
      </z-spot>
    </section>
  </z-view>
</template>

<script>
import { mapState } from "vuex";

// const coloresLGTB = [
//   "#FF0018",
//   "#FFA52C",
//   "#FFFF41",
//   "#008018",
//   "#0000F9",
//   "#86007D"
// ];

const items = [
  {
    texto: "Web",
    icon: "public",
    color: "#FFA52C"
    //items: [{}, {}, {}]
  },
  {
    texto: "Mobile",
    icon: "phone_iphone",
    color: "#FFFF41"
    //items: [{}, {}, {}]
  },
  {
    texto: "Aplicaciones",
    icon: "desktop_windows",
    color: "#FFA52C"
    //items: [{}, {}, {}]
  },
  {
    texto: "Estadisticas",
    icon: "bar_chart",
    color: "#FFFF41",
    view: "estadistica"
    //items: [{}, {}, {}]
  }
];

export default {
  data() {
    return {
      items
    };
  },
  computed: {
    ...mapState(["angulo"])
    // anguloRelativo() {
    //   return index => (360 / this.items.length) * index - this.angulo * 11;
    // }
  },
  methods: {
    // colorRandom() {
    //   const index = Math.floor(Math.random() * coloresLGTB.length);
    //   return coloresLGTB[index];
    // },
    anguloRelativo(index, count, velocidad) {
      return (360 / count) * index - this.angulo * velocidad;
    },
    pausar() {
      const animacion = this.$root.$animacion;

      animacion.paused() ? animacion.play() : animacion.pause();
      //console.log(this.$root.$animacion); // eslint-disable-line
    }
  }
};
</script>

<style scoped>
.centro {
  /* background-color: Green; */
  font-family: "Roboto", sans-serif;
  text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000,
    -1px 1px 0 #000, 1px 1px 0 #000;
}

.centro .titulo {
  color: white;
}

.primer-nivel {
  background-color: purple;
  color: white;
  /* font-size: medium;
  font-family: auto; */
}
</style>
